import {
  Typography,
  AppBar,
  Toolbar,
  Box,
  IconButton,
  Avatar,
} from '@mui/material';
import { ExitToApp, Menu } from '@mui/icons-material';
import { Link, useLocation } from 'react-router-dom';
import Logo from 'src/components/auxiliares/Logo.js';
import { sideBarOptions } from 'src/components/auxiliares/sideBarOptions.js';
import { useNavigate } from 'react-router-dom';
//import { useQuery } from 'react-query';
//import { getMethod } from 'src/utils/api';

export default function DashboardNavbar({
  setisMobileNavOpen,
  setLoggedUser,
  idSociety,
  loggedUser,
}) {

  var pathAvatar = '/static/images/avatars/' + loggedUser.avatar;

  const user = {avatar: pathAvatar,};

  const navigate = useNavigate();

  const { pathname } = useLocation();

  // const lastPartOfPath = pathname.split('/').at(-1);
  const lastPartOfPath = pathname.split('/')[pathname.split('/').length - 1];

  if(!loggedUser){
    logOut();
  }

  const nameOfPage =
    sideBarOptions.find(option => option.path === lastPartOfPath)?.title ||
    lastPartOfPath;

  //const [notifications] = useState([]);
  function logOut() {
    setLoggedUser(null);
    navigate(`../${idSociety?.nombre}`);
  }

  /****************************************************/
  /*************** deuda tecnica **********************/
  /****************************************************/
/*
  const { data: bancos } = useQuery(['bancos', idSociety], () =>
    getMethod(`banco/listar/${idSociety.id}`)
  );
  localStorage.setItem("bancos", JSON.stringify(bancos));

  const { data: cuentasbanco } = useQuery(['cuentasbanco', idSociety], () =>
    getMethod(`cuentabanco/listar/${idSociety.id}/0`)
  );  
  localStorage.setItem("cuentasbanco", JSON.stringify(cuentasbanco));*/

  /****************************************************/
  /****************************************************/
  /****************************************************/

  return (
    <AppBar elevation={0}>
      <Toolbar>
        <Link to="/">
          <Logo idSociety={idSociety} />
        </Link>
        <div style={{ marginLeft: 50, fontSize: 24 }}>
          <Typography align="left" color="white" variant="h4">
            {nameOfPage
              .replace('-', ' de ')
              .replace('%20', ' ')
              .replace('%20', ' ')
              .replace('%20', ' ')
              .replace('_', ': ')}
          </Typography>
        </div>
        <Box sx={{ flexGrow: 1 }} />

        <div>
          <Typography align="left" color="white" variant="h4">
            {loggedUser?.user}
          </Typography>
        </div>

        <IconButton color="inherit">
          <Avatar
            component={Link}
            to="/"
            src={user.avatar}
            sx={{
              cursor: 'pointer',
              width: 30,
              height: 30,
            }}
          />
        </IconButton>
        <IconButton
          color="inherit"
          sx={{ display: { md: 'block', lg: 'none' } }}
          onClick={setisMobileNavOpen}
        >
          <Menu />
        </IconButton>
        <IconButton color="inherit" onClick={logOut}>
          <ExitToApp />
        </IconButton>
      </Toolbar>
    </AppBar>
  );
}
